  background-image: url(../assets/svg/tree.svg)\r\n    background-size: contain\r\n    background-repeat: no-repeat\r\n    &:hover\r\n      filter: invert(57%) sepia(100%) saturate(329%) hue-rotate(141deg)brightness(89%) contrast(90%)\r\n\r\n\r\n  &-search\r\n    display: flex\r\n    align-items: center\r\n    &__input\r\n      padding: 5px 25px\r\n      margin-right: 2em\r\n      height: 35px\r\n      width: 380px\r\n      border-radius: 2em\r\n      background-color: $mc\r\n      border: 1px solid $tc\r\n      color: #fff\r\n      text-transform: uppercase\r\n      &:focus\r\n        outline: none\r\n    &__favourites\r\n      display: flex\r\n      align-items: center\r\n      justify-content: center\r\n      background-image: url(../assets/svg/ball-2.svg)\r\n      background-size: 48px 60px\r\n      background-position: left -8px\r\n      background-repeat: no-repeat\r\n      width: 48px\r\n      height: 60px\r\n      span\r\n        display: flex\r\n        align-items: center\r\n        justify-content: center\r\n        background-color: $tc\r\n        width: 87%\r\n        height: 67%\r\n        border-radius: 50%\r\n",".footer\r\n  @extend .navbar\r\n\r\n  &__about\r\n  &__link\r\n    margin-left: 30px\r\n    &:hover\r\n      color: $tc\r\n  &__logo\r\n    width: 160px\r\n    height: 60px\r\n    background-image: url(../assets/svg/rss.svg)\r\n    background-size: contain\r\n    background-repeat: no-repeat\r\n    &:hover\r\n      filter: invert(57%) sepia(100%) saturate(329%) hue-rotate(141deg)brightness(89%) contrast(90%)",".border\r\n  background-color: $mc\r\n  border: 1px solid $tc\r\n  border-radius: 25px\r\n  backdrop-filter: blur(6px)\r\n\r\n.button\r\n  outline: 1px solid\r\n  outline-color: rgba(255, 255, 255, 0.4)\r\n  outline-offset: 0px\r\n  transition: all 1250ms cubic-bezier(0.19, 1, 0.22, 1)\r\n  color: #fff\r\n  &:hover\r\n    cursor: pointer\r\n    outline-offset: 1em\r\n    outline-color: rgba(255, 255, 255, 0.0)\r\n\r\n\r\n.home\r\n  padding-top: 300px\r\n  position: relative\r\n  min-width: 100%\r\n  min-height: calc(100vh - 140px)\r\n  background-image: url(../assets/bg.jpg)\r\n  background-size: cover\r\n  background-attachment: fixed\r\n  color: #fff\r\n  text-align: center\r\n\r\n  &__title\r\n    margin: 0 auto\r\n    margin-bottom: 70px\r\n    padding: 66px 85px\r\n    font-size: 53px\r\n    line-height: 58px\r\n    text-align: center\r\n    max-width: 650px\r\n    font-family: 'Neucha', cursive\r\n    @extend .border\r\n\r\n  \r\n  &__btn\r\n    padding: 13px 119px\r\n    font-weight: 400\r\n    font-size: 24px\r\n    line-height: 29px\r\n    @extend .border\r\n    @extend .button\r\n\r\n\r\n.ball\r\n  position: absolute\r\n  background-size: contain\r\n  background-repeat: no-repeat\r\n  &::after\r\n    content: \"\"\r\n    position: absolute\r\n    background-color: #aaa\r\n    z-index: 0\r\n  \r\n  &-big\r\n    width: 500px\r\n    height: 500px\r\n    left: calc(50% - 550px)\r\n    top: 100px\r\n    background-image: url(../assets/ball/1.png)\r\n    &::after\r\n      top: -210px\r\n      left: calc(50% - 47px)\r\n      width: 4px\r\n      height: 220px\r\n      width: 5px\r\n\r\n  &-small\r\n    width: 300px\r\n    height: 300px\r\n    left: calc(50% + 150px)\r\n    left: 50%\r\n    top: 150px\r\n    background-image: url(../assets/ball/2.png)\r\n    &::after\r\n      top: -205px\r\n      left: calc(50% - 32px)\r\n      width: 4px\r\n      height: 210px\r\n      width: 5px\r\n\r\n",".main\r\n  min-width: 100%\r\n  // min-height: calc(100vh - 140px)\r\n  overflow: hidden\r\n  display: flex\r\n\r\n.block-title\r\n  margin: 15px 0\r\n  font-size: 18px\r\n  line-height: 22px\r\n  font-family: 'Neucha', cursive\r\n.filters\r\n  padding: 2% 1% 1% 2%\r\n  position: static\r\n  height: 100%\r\n  min-width: 400px\r\n  height: calc( 100vh - 70px )\r\n  background-color: $bg\r\n  overflow-y: auto\r\n  &::-webkit-scrollbar\r\n      width: 0\r\n  &-block\r\n    margin-bottom: 35px\r\n\r\n// class extends\r\n.icon\r\n  width: 36px\r\n  height: 36px\r\n  background-size: contain\r\n  background-repeat: no-repeat\r\n  cursor: pointer\r\n  transition: all .5s\r\n  &:hover\r\n    filter: invert(9%) sepia(0%) saturate(461%) hue-rotate(322deg) brightness(70%) contrast(88%)\r\n\r\n.check\r\n  &__label\r\n    display: flex\r\n    align-items: center\r\n    column-gap: 5px\r\n  &__input\r\n    appearance: none\r\n    background-color: $mc\r\n    margin: 0\r\n    font: inherit\r\n    color: green\r\n    width: 24px\r\n    height: 24px\r\n    border: 1px solid $tc\r\n    border-radius: 20%\r\n    transform: translateY(-0.075em)\r\n    display: grid\r\n    place-content: center\r\n    cursor: pointer\r\n    &::before\r\n      content: \"\"\r\n      width: 0.65em\r\n      height: 0.65em\r\n      clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%)\r\n      transform: scale(0)\r\n      transform-origin: bottom left\r\n      transition: 120ms transform ease-in-out\r\n      box-shadow: inset 1em 1em #fff\r\n      background-color: #fff\r\n    &:checked::before\r\n      transform: scale(1)\r\n    &:checked\r\n      background-color: $tc\r\n\r\n.section-title\r\n  padding-top: 20px\r\n  padding-left: 20px\r\n  text-transform: uppercase\r\n  font-size: 42px\r\n  line-height: 46px\r\n  font-family: 'Neucha', cursive\r\n\r\n.icon-active\r\n  filter: invert(9%) sepia(63%) saturate(461%) hue-rotate(322deg) brightness(83%) contrast(88%)\r\n\r\n\r\n.search\r\n  display: flex\r\n  &-input\r\n    @extend .navbar-search__input\r\n    width: 100%\r\n\r\n.sort\r\n  display: flex\r\n  align-items: center\r\n  column-gap: 30px\r\n  &__select\r\n    @extend .border\r\n    @extend .button\r\n    padding: 0.3em 1.3em\r\n\r\n.forms\r\n  &-wrapper\r\n    display: flex\r\n    justify-content: space-between\r\n    font-size: 12px\r\n    line-height: 14px\r\n    flex-wrap: wrap\r\n  &__item\r\n    display: flex\r\n    justify-content: space-between\r\n    align-items: center\r\n    flex-direction: column-reverse\r\n    row-gap: 15px\r\n    cursor: pointer\r\n    @extend .icon\r\n\r\n.colors\r\n  display: flex\r\n  justify-content: space-between\r\n  max-width: 200px\r\n  &__label\r\n    @extend .check__label\r\n\r\n  &__input\r\n    @extend .check__input\r\n    border: none\r\n    transition: transform .3s\r\n    &:hover\r\n      transform: scale(1.1)\r\n    &::before\r\n      box-shadow: inset 1em 1em #000\r\n      background-color: #000\r\n\r\n#checkWhite\r\n  background-color: #fff\r\n#checkYellow\r\n  background-color: #FDD700\r\n#checkRed\r\n  background-color: #FD0000\r\n#checkBlue\r\n  background-color: #2299EB\r\n#checkGreen\r\n  background-color: #08AA05\r\n\r\n.size\r\n  &__block\r\n    display: flex\r\n    align-items: center\r\n    justify-content: space-between\r\n\r\n//=----------------CARDS---------------------\r\n.cards\r\n  overflow: hidden\r\n  height: calc( 100vh - 70px )\r\n  overflow-y: auto\r\n  &::-webkit-scrollbar\r\n      width: 0\r\n  &-wrapper\r\n    padding: 20px\r\n    display: flex\r\n    flex-wrap: wrap\r\n    justify-content: center\r\n    gap: 36px\r\n\r\n  &__no-matches\r\n    margin: 0 auto\r\n    font-family: \"Neucha\", cursive\r\n    font-size: 30px\r\n    line-height: 30px\r\n\r\n.card\r\n  padding: 15px 20px\r\n  display: flex\r\n  flex-direction: column\r\n  justify-content: space-between\r\n  width: 222px\r\n  height: 342px\r\n  font-size: 12px\r\n  line-height: 12px\r\n  border-radius: 10px\r\n  background: radial-gradient(109.56% 109.56% at 0% -2.94%, rgba(255, 255, 255, 0.5) 0%, rgba(179, 246, 255, 0.5) 100%)\r\n  backdrop-filter: blur(4px)\r\n  cursor: pointer\r\n  transition: all 1s\r\n  &_active\r\n    background: radial-gradient(109.56% 109.56% at 0% -2.94%, rgba(255, 158, 161, 0.5) 0%, rgba(179, 246, 100, 0.5) 100%)\r\n  &__title \r\n    font-size: 18px\r\n    line-height: 20px\r\n    font-family: Neucha\r\n  &__img\r\n    width: 85px\r\n    height: 85px\r\n    background-image: url(../assets/toys/1.png)\r\n    background-size: contain\r\n    background-repeat: no-repeat\r\n    align-self: center\r\n  &__total \r\n  &__year \r\n  &__form \r\n  &__size \r\n  &__favourite \r\n  &__favourite-icon \r\n\r\n.btns\r\n  display: flex\r\n  justify-content: space-evenly\r\n  &__reset\r\n    @extend .border\r\n    @extend .button\r\n    padding: 0.5em 1em\r\n\r\n//------sliders-------\r\n.double-input\r\n  margin: 0 auto\r\n  height: 2px\r\n  width: calc( 100% - 14px )\r\n.noUi-target\r\n  background-color: $tc\r\n  border-radius: 2px\r\n  border: none\r\n.noUi-base\r\n.noUi-connect\r\n  background-color:  $tc\r\n.noUi-origin\r\n\r\n.noUi-touch-area\r\n\r\n.noUi-handle \r\n  box-shadow: none\r\n  border: 2px solid $tc\r\n  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), radial-gradient(57.23% 248.2% at 107.17% 104.02%, rgba(34, 153, 235, 0.92) 0%, rgba(34, 153, 235, 0.07) 100%), radial-gradient(104.22% 230.94% at -4.22% 0%, #24C5DB 3.62%, rgba(36, 197, 219, 0.26) 55.1%)\r\n  border-radius: 50%\r\n  cursor: pointer\r\n\r\n  &::before, &::after\r\n    display: none\r\n\r\n\r\n.noUi-horizontal .noUi-handle \r\n  width: 15px\r\n  height: 15px\r\n  right: -7px\r\n\r\n\r\n.year\r\n  margin-top: 20px\r\n  display: flex\r\n  justify-content: space-between\r\n\r\n\r\n@media (max-width: 1024px)\r\n\r\n  .filters\r\n    min-width: 330px\r\n    width: 400px\r\n\r\n  .cards\r\n    &-wrapper\r\n      gap: 20px\r\n      justify-content: center\r\n\r\n  .card\r\n    width: 175px\r\n    height: 300px\r\n    &__img\r\n      width: 80px\r\n\r\n  .size__block\r\n    flex-direction: column\r\n    align-items: start\r\n    row-gap: 10px\r\n",".main-tree\r\n  display: flex\r\n  justify-content: space-between\r\n  max-width: 95%\r\n  min-height: calc(100vh - 140px)\r\n  margin: 0 auto\r\n\r\n.settings\r\n  max-width: 400px\r\n  padding-top: 1%\r\n  display: flex\r\n  flex-direction: column\r\n  &-block\r\n    @extend .filters-block\r\n\r\n  &-app\r\n    display: flex\r\n  &__sound\r\n    @extend .icon\r\n    background-image: url(../assets/svg/audio.svg)\r\n  &__snow\r\n    margin-left: 30px\r\n    @extend .icon\r\n    background-image: url(../assets/svg/snow.svg)\r\n\r\n  &__tree\r\n    &-block\r\n      display: flex\r\n      flex-wrap: wrap\r\n      gap: 20px\r\n    &-item\r\n      width: 100px\r\n      height: 100px\r\n      border-radius: 10px\r\n      background-color: rgba(255, 255, 255, .5)\r\n      background-size: auto 100px\r\n      background-position: center\r\n      background-repeat: no-repeat\r\n      cursor: pointer\r\n      &:nth-child(1)\r\n        background-image: url(../assets/tree/1.png)\r\n      &:nth-child(2)\r\n        background-image: url(../assets/tree/2.png)\r\n      &:nth-child(3)\r\n        background-image: url(../assets/tree/3.png)\r\n      &:nth-child(4)\r\n        background-image: url(../assets/tree/4.png)\r\n      &:nth-child(5)\r\n        background-image: url(../assets/tree/5.png)\r\n      &:nth-child(6)\r\n        background-image: url(../assets/tree/6.png)\r\n\r\n\r\n  &__background\r\n    &-block\r\n      @extend .settings__tree-block\r\n      &:last-child\r\n        justify-self: start\r\n    &-item\r\n      @extend .settings__tree-item\r\n      width: 70px\r\n      height: 70px\r\n      &:nth-child(1)\r\n        background-image: url(../assets/bg/bg1.jpg)\r\n      &:nth-child(2)\r\n        background-image: url(../assets/bg/bg2.jpg)\r\n      &:nth-child(3)\r\n        background-image: url(../assets/bg/bg3.jpg)\r\n      &:nth-child(4)\r\n        background-image: url(../assets/bg/bg4.jpg)\r\n      &:nth-child(5)\r\n        background-image: url(../assets/bg/bg5.jpg)\r\n      &:nth-child(6)\r\n        background-image: url(../assets/bg/bg6.jpg)\r\n      &:nth-child(7)\r\n        background-image: url(../assets/bg/bg7.jpg)\r\n      &:nth-child(8)\r\n        background-image: url(../assets/bg/bg8.jpg)\r\n        \r\n\r\n  &__garland\r\n    &-block\r\n      @extend .settings__tree-block